// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'


/*  mint-ui  axios*/

import './assets/css/base.css'

import Qs from 'qs';
import axios from 'axios';
axios.defaults.withCredentials=true; //cookie
import VueAxios from 'vue-axios';

// import '../static/css/normalize.css'
// import '../static/js/rem.js'



import cookie from 'js-cookie'

import receiveData from './receiveData.js'
import common from './common_v3.js'
import $ from 'jquery'

// Vue.prototype.common = common;

Vue.prototype.receiveData = receiveData;
Vue.prototype.common = common;

//let str = 'https://test.e-shequ.com/guangming/weixin/';
let str = 'https://www.e-shequ.com/guangming/wechat/hexie/wechat/';
// let str = 'https://www.e-shequ.com/guangming/hexie/wechat/';
Vue.prototype.str = str;

let baseUrl = 'https://www.e-shequ.com/guangming/wechat/hexie/wechat';
Vue.prototype.baseUrl = baseUrl;

// let bindAppId = "wx95f46f41ca5e570e";
// Vue.prototype.bindAppId = bindAppId;

// let oauthUrl = "http://open.weixin.qq.com/connect/oauth2/authorize?";
// Vue.prototype.oauthUrl = oauthUrl;

// let oauthUrlPostFix = "&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect";
// Vue.prototype.oauthUrlPostFix = oauthUrlPostFix;

// let appId = "wx95f46f41ca5e570e";
// Vue.prototype.appId = appId;


//axios 

var axiosInstance = axios.create({
    transformRequest: [function (data) {
        //data = Qs.stringify(data);
        data = JSON.stringify(data);
        return data;
    }],
    headers: {
        //'Content-Type': 'application/x-www-form-urlencoded',
        //'Content-Type':'application/json;charset=utf-8',
        'Content-Type':"application/json",

        //'Accept': '*/*',
        'Accept': 'application/json',

    },
    baseURL: 'https://www.e-shequ.com/guangming/wechat/hexie/wechat',
    //baseURL:'http://10.0.8.20/',
    withCredentials:true,
    transformResponse: [function (data) {
      return data;
    }],
});
//axios cookie

axiosInstance.interceptors.request.use(
    config => {
        if( !cookie.get('session')){//seesion   seesion
          
        }else{// session
          config.headers.Cookie =`${ cookie.get('Cookie') }`
        }

        // config.headers.Authorization = // token
        //     `${ Cookies.get('yesmywine_mall$token_type') } ${ Cookies.get('yesmywine_mall$token') }`
         //  return config http
         return config
    },
    err => {
        return Promise.reject(err)
    }
)

Vue.use(VueAxios, axiosInstance);


//
Vue.config.productionTip = false
// debug
Vue.config.debug = true;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



